<template>
    <div>
      <common-header :h_title="h_title" :showback="true" :bgcolor='head_color'></common-header>
      <div class="m_height" v-if="myWallet">
       <div style='height:0.9rem'></div>
        <div class="myWallet" >
          <div class='m_bg_top'>
              <div class='m_bg_top_t'>
                  <img :src="imgPath+'shop/cash_1.png'" class='m_bg_top_bg' mode='widthFix'/>
                  <div class='m_bg_top_content'>
                    <img :src="imgPath+'shop/cash_2.png'" class='m_bg_top_content_bg' mode='widthFix'/>
                    <div class='m_balance'>
                        <div class='m_balance_l'>
                            <span class='m_balance_con'>我的余额：</span>
                            <span class='m_balance_count'>￥{{membeInfo.money}}</span>
                        </div>
                        <div class='m_balance_r'>
                            <div class='m_balance_r_t' @click='showRules'>
                                <span>提现规则</span>
                                <i class='iconfont icon-iconset0143'></i>
                            </div>
                            <span class='m_cash' @click='toCashWith'>提现</span>
                        </div>
                    </div>
                    <div class='m_all_money'>
                        <span class='m_y_money'>可提现：￥{{membeInfo.can_money}}</span>
                        <span class='m_n_money'>待结算：￥{{membeInfo.money_freeze}}</span>
                    </div>
                  </div>
              </div>
              <div class='m_Detailed'>
                <div class='detailed_nav'>
                    <div @click='etailedType("1")' :class="detailType=='1'?'text_active':'text1'" id='1'>收益明细</div>
                    <div @click='etailedType("2")' :class="detailType=='2'?'text_active':'text1'" id='2'>提现记录</div>
                </div>
                <div class='detailed_choseDate'>
                    <div class='detailed_choseDate_inner'>
                      <div class='detailed_choseDate_show'>
                          <div class='detailed_choseDate_show_l' @click='openBirth'>
                              <div class='pickDate'>
                                  <span v-if='birthday!=null'>{{birthday}}</span>
                                  <!-- <span v-if='{{birthday==null}}'>{{nowDate}}</span> -->
                                  <i class='iconfont icon-desc'></i>
                                  <!-- <icon class='{{dowmList?"iconfont icon-asc":"iconfont icon-desc"}}'></icon> -->
                              </div>
                              <!-- </picker> -->
                          </div>
                          <div class='detailed_choseDate_show_r'>
                              <div class='m_income' v-if="detailType=='1'" >
                              收入<span class='m_c'>￥{{cashMoney.income}}</span>
                              </div>
                              <div class='m_waitCash' v-if="detailType=='1'">
                              待结算<span class='m_c'>￥{{cashMoney.stay_wind}}</span>
                              </div>
                              <div class='m_income' v-if="detailType=='2'" >
                              累计提现<span class='m_c'>￥{{cashMoney.income}}</span>
                              </div>
                              <div class='m_waitCash' v-if="detailType=='2'" >
                              提现<span class='m_c'>￥{{cashMoney.stay_wind}}</span>
                              </div>
                          </div>
                      </div>
                    </div>
                </div>
              </div>
          </div>
          <div v-infinite-scroll="loadMore" infinite-scroll-disabled="is_load" infinite-scroll-distance="30" @scroll="scrollEvent" class="scroll" id='scroll'>
              <!-- <div class='head_top'></div> -->
              <div id='returnTop' style="height:0.01rem"></div>
              <div class="returnTop" v-if="show" @click="goTop">
                  <i class="iconfont icon-huidaodingbu"></i>
              </div>
              <div ref="vieBox">
                <div class='m_Detailed_List' v-if='total!=0'>
                    <div class='list_info' v-if="detailType=='1'" v-for="(listblock,index) in detailList" :key="index">
                        <div class='list_info_content' v-for="item in listblock" :key="item.id" report-submit="true">
                            <img class='list_info_timg' v-if="item.type!=1&&item.status!=2" :src="imgPath+'shop/share_icon.png'"/>
                            <img class='list_info_timg' v-if="item.type!=1&&item.status==2" :src="imgPath+'shop/share_icon1.png'"/>
                            <img class='list_info_timg' v-if="item.type==1&&item.status!=2" :src="imgPath+'shop/share_icon3.png'"/>
                            <img class='list_info_timg' v-if="item.type==1&&item.status==2" :src="imgPath+'shop/share_icon2.png'"/>
                            <div class='list_info_br'>
                              <div class='list_info_tright'  v-on:click.prevent.stop='showCashInfo(item.id)' >
                                  <div class='list_info_tright_l'>
                                  <span class='list_info_tright_lt' v-if="item.type!=1">分享佣金</span>
                                  <span class='list_info_tright_lt' v-if="item.type==1">购物返利</span>
                                  <div class='list_info_tright_lb'>
                                      <span>{{item.createtime}}</span>
                                  </div>
                                  </div>
                                  <div class='list_info_tright_r'>
                                  <div class='list_info_tright_r1' v-if="item.status!=2&&item.status!=3">
                                      <div class='list_info_tright_r1_t'>
                                      <span>+{{item.money}}</span>
                                      <i :class="choseId==item.id && myWalletShow ? 'iconfont icon-xiala1':'iconfont icon-jiantouarrow487'"></i>
                                      </div>
                                      <div class='list_info_tright_r1_b' v-if="item.status==0">待结算</div>
                                  </div>
                                  <div class='list_info_tright_r2 xian' v-if="item.status==2">
                                      <span>+{{item.money}}</span>
                                      <i class="iconfont icon-jiantouarrow487"></i>
                                      <div>已退款</div>
                                      <span class='xiexian'></span>
                                  </div>
                                  <div class='list_info_tright_r2 xian' v-if="item.status==3">
                                      <span>+{{item.money}}</span>
                                      <i class="iconfont icon-jiantouarrow487"></i>
                                      <div>降级退还</div>
                                      <span class='xiexian'></span>
                                  </div>
                                  </div>
                              </div>
                              <div class='list_info_bright' v-if="choseId==item.id&&myWalletShow">
                                  <div class='list_info_bright_l'>
                                  <img :src='item.buyer.image'/>
                                  <div class='list_info_bright_ti'>
                                      <span>{{item.buyer.nickname}}</span>
                                  </div>
                                  </div>
                                  <div class='list_info_bright_r'>
                                  <!-- <div>{{item.order[0].name}}</div> -->
                                  <div v-if="item.status=='0'">待结算</div>
                                  <div v-if="item.status=='1'">已领取</div>
                                  <div v-if="item.status=='2'">已退款</div>
                                  <div v-if="item.status=='3'">降级退还</div>
                                  <!-- <div>下单时间 <span>{{item.paytime}}</span></div> -->
                                  </div>
                              </div>
                            </div>
                        </div>
                    </div>
                    <div class='list_info' v-if="detailType==='2'||detailType===2" v-for="(item,index) in detailList" :key="index">
                        <div class='list_info_content' v-for="(item,index) in listblock" :key="index" :data-id="index" report-submit="true">
                        <img class='list_info_timg' :src="imgPath+'shop/share_icon.png'"/>
                        <div class='list_info_br'>
                            <div class='list_info_tright'>
                            <div class='list_info_tright_l'>
                                <span class='list_info_tright_lt cashSuccess' >提现成功</span>
                                <div class='list_info_tright_lb'>
                                <span>{{item.createtime}}</span>
                                </div>
                            </div>
                            <div class='list_info_tright_r'>
                                <div class='list_info_tright_r1'>
                                <div class='list_info_tright_r1_t cashMoney'>
                                    <span>-{{item.money}}</span>
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>
                        </div>
                    </div>
                </div>
                <div class="mar_bottom">
                    <div class='more_load' v-if='!is_load'>
                        <mt-spinner type="fading-circle"></mt-spinner>
                    </div>
                    <div class='s_fans_bottom' v-if='!hasData'>
                        <img :src="imgPath+'pro/null.png'" mode='widthFix'/>
                    </div>
                    <div class='order-foot' v-if="hasData&&is_end">
                        <span>—————— 我是有底线的 ——————</span>
                    </div>
                  </div>
                </div>
              </div>
          </div>
        </div>
        <mt-datetime-picker
          ref="dataPicker"
          type="date"
          v-model="dateVale"
          year-format="{value} 年"
          month-format="{value} 月"
          date-format="{value} 日"
          @confirm="bindDateChange"
          :startDate = "startDate"
          :endDate = "endDate">
        </mt-datetime-picker>
        <div class='rulesDetail_bg' v-if='rulesShow' @click='hiddenRules'></div>
        <div class='rulesDetail' v-if='rulesShow'>
          <div class='rules_head'>提现规则说明</div>
          <div class='rules_richtext' style='height: 3.4rem;'>
              <span>{{rulesTitle}}</span>
          </div>
          <div class='rules_btn' @click='hiddenRules'>
              <mt-button>我知道了</mt-button>
          </div>
        </div>
      </div>
</template>
<script>
import commonHeader from 'common/common-header'
import returnTop from '@/components/return-top'
import {
  InfiniteScroll,
  Indicator,
  Picker,
  DatetimePicker,
  Toast,
  Button,
  Loadmore
} from 'mint-ui'
import {
  getCashInfo, getShoperMoney, getWalletList
} from '@/api/share'
import {
  parseTime
} from '@/store/pubilc.js'
export default {
  name: 'MyWallet',
  data() {
    return {
      h_title: '我的钱包',
      head_color: {
        background: '#f3c0a2',
        color: '#000'
      },
      choseId: -1,
      myWalletShow: false,
      myWallet: false,
      startDate: new Date('1971-01-01'),
      endDate: new Date('2019-01-22'),
      // is_iphonex: app.globalData.phoneType,
      isSign: true,
      membeInfo: {},
      imgPath: process.env.FilE_URL,
      detailType: '1',
      fifterHeight: '',
      dateVale: parseTime(new Date().getTime(), '{y}-{m}-{d}'),
      detailList: [],
      cashMoney: {
        income: '0.00',
        stay_wind: '0.00'
      },
      height: '',
      topNum: 0,
      total: 0,
      allowScorll: true,
      rulesShow: false,
      rulesTitle: '',
      birthday: null,
      cashInfo: false,
      flag: true,
      hidenShow: false,
      cashInfoNum: 1,
      is_end: false,
      hasData: true,
      is_load: true,
      page: 1,
      dataInfo: {},
      nowDate: '',
      show: false
    }
  },
  components: {
    InfiniteScroll,
    Indicator,
    DatetimePicker,
    commonHeader,
    Picker,
    Toast,
    Button,
    returnTop,
    Loadmore
  },
  mounted() {
    Indicator.open('加载中...')
    const that=this
    var nowDates = parseTime(new Date().getTime())
    var nowDate = nowDates.split(' ')[0]
    var year = nowDate.split('-')[0]
    var month = nowDate.split('-')[1]
    var showDate = year + '年' + month + '月'
    this.endDate = new Date()
    // this.date = nowDates
    this.nowDate = nowDate.substring(0, nowDate.length - 3)
    this.birthday = showDate
    this.getFindCon()
    // 获取钱包列表
    this.getAcountList()
    // 获取金额信息
    this.getVipInfo()
  },
  methods: {
    scrollEvent(event){
      if(event.target.scrollTop>600){
        this.show=true
      }else{
        this.show=false
      }
    },
    //回到顶部
    goTop() {
      console.log('回到顶部')
      document.getElementById('returnTop').scrollIntoView({
        block: 'start',
        behavior: 'smooth'
      })
    },
    // 获取提现规则查看说明内容
    getFindCon() {
      const that = this
      getCashInfo().then(res => {
        if (res.isSuccess) {
          that.rulesTitle = res.data.member_withdraw
        }
      })
    },
    // 显示提现规则弹框
    showRules() {
      this.rulesShow = true
      this.allowScorll = false
    },
    // 关闭提现规则
    hiddenRules() {
      this.rulesShow = false
      this.allowScorll = true
    },
    // 跳转到提现
    toCashWith() {
      this.$router.togo({
        path: '/cashWithDrawal'
      })
    },
    // 列表上面点击事件 是否显示折叠内容
    showCashInfo(id) {
      console.log(id, '当前id')
      const that = this
      // var current = id
      for (let i = 0; i < this.detailList.length; i++) {
        for (let j = 0; j < this.detailList[i].length; j++) {
          if (this.detailList[i][j].id === id) {
            if (this.detailList[i][j].status !== '3' && this.detailList[i][j].status !== '2') {
              // this.detailList[i][j].hidden = !this.detailList[i][j].hidden
              // console.log(this.detailList[i][j].hidden, '2222')
              that.choseId = id
              that.myWalletShow = !that.myWalletShow
              console.log(that.choseId)
            } else {
              that.choseId = -1
              that.myWalletShow = false
            }
          }
        }
      }
      // this.$set(this.detailList)
      // // this.detailList = this.detailList
      // console.log(this.detailList)
    },
    openBirth() {
      this.$refs.dataPicker.open()
      let a = document.getElementsByClassName('picker-slot')
      // eslint-disable-next-line no-unmodified-loop-condition
      for (let i = 0; a && i < a.length; i++) {
        if (i === 2) {
          a[i].style.display = 'none'
        }
      }
    },
    // 选择日期
    bindDateChange(e) {
      console.log(e.getTime(), '1111')

      var dateTime = parseTime(e.getTime(), '{y}-{m}-{d}')
      var year = dateTime.split('-')[0]
      var month = dateTime.split('-')[1]
      var showDate = year + '年' + month + '月'
      this.birthday = showDate
      this.nowDate = dateTime.substring(0, dateTime.length - 3)
      this.detailList = []
      this.page = 1
      this.getAcountList()
    },
    // 激活状态
    etailedType(id) {
      this.detailType = id
      this.is_end = false
      this.page = 1
      this.dateType = '1'
      this.detailList = []
      this.getAcountList()
    },
    // 获取金额信息
    getVipInfo() {
      const that = this
      Indicator.open('加载中...')
      getShoperMoney().then(res => {
        if (res.isSuccess) {
          that.myWallet = true
          Indicator.close()
          that.membeInfo = res.data.member
        }
      })
    },
    // 获取列表信息
    getAcountList: function(e) {
      const that = this
      var obj = {
        page: that.page,
        type: that.detailType,
        date: that.nowDate
      }
      Indicator.open('加载中...')
      getWalletList(obj).then(res => {
        if (res.isSuccess) {
          Indicator.close()
          if (that.detailType === 1 || that.detailType === '1') {
            if (res.data.money.rows.length > 0) {
              let currentpage = that.page - 1
              console.log(that.page)
              if (currentpage === 0) {
                that.detailList = []
              }
              that.detailList[currentpage] = res.data.money.rows
              // par: res.page,
              that.total = res.data.money.total
              that.cashMoney = res.data.wind
              that.hasData = true
              that.is_end = false
              that.is_load = false
              // 如果一页加载数据等于总条数
              if (res.data.money.rows.length === parseInt(res.data.money.total) && res.data.money.rows.length > 0) {
                that.cashMoney = res.data.wind
                that.is_load = true
                that.is_end = true
              }
            } else {
              that.cashMoney = res.data.wind
              that.is_load = true
              that.hasData = false
            }
          } else {
            if (res.data.advance.rows.length > 0) {
              let currentpage = that.page - 1
              console.log(that.page)
              if (currentpage === 0) {
                that.detailList = []
              }
              that.detailList[currentpage] = res.data.advance.rows
              that.total = res.data.advance.total
              that.cashMoney = res.data.wind
              that.hasData = true
              that.is_end = false
              that.is_load = false
              // 如果一页加载数据等于总条数
              if (res.data.advance.rows.length === Number(res.data.advance.total) && res.data.advance.rows.length > 0) {
                that.cashMoney = res.data.wind
                that.is_load = true
                that.is_end = true
              }
            } else {
              that.cashMoney = res.data.wind
              that.is_load = true
              that.hasData = false
            }
          }
        } else {
          Toast({
            message: res.msg,
            duration: 2000
          })
        }
      })
    },
    // 上拉加载更多
    loadMore() {
      var that = this
      var obj = {
        page: ++that.page,
        type: that.detailType,
        date: that.date
      }
      if(that.page>2){
        that.show=true
      }else{
        that.show=false
      }
      this.is_load = true
      // if (this.is_load === false) {
      if (that.detailType === 1 || that.detailType === '1') {
        getWalletList(obj).then(res => {
          if (res.isSuccess === true || res.isSuccess === 'true') {
            if (res.data.money.rows.length > 0) {
              let currentpage = that.page - 1
              that.detailList[currentpage] = res.data.money.rows
              that.cashMoney = res.data.wind
              that.is_end = false
              that.is_load = false
              if (JSON.stringify(res.data.money.rows) === '[]' || that.detailList[currentpage].length < 10) {
                that.cashMoney = res.data.wind
                that.is_load = true
                that.is_end = true
                console.log('11111')
              }
            }
          } else {
            that.cashMoney = res.data.wind
            that.is_end = true
            that.is_load = true
          }
          console.log(that.is_end)
        })
      } else {
        getWalletList(obj).then(res => {
          if (res.isSuccess === true || res.isSuccess === 'true') {
            console.log(res.rows)
            if (res.data.advance.rows.length > 0) {
              ++that.page
              let currentpage = that.page - 1
              that.detailList[currentpage] = res.data.advance.rows
              that.is_end = false
              that.is_load = false
              if (JSON.stringify(res.data.advance.rows) === '[]' || that.detailList[currentpage].length < 10) {
                that.cashMoney = res.data.wind
                that.is_load = true
                that.is_end = true
              }
            }
          } else {
            that.cashMoney = res.data.wind
            that.is_end = true
            that.is_load = true
          }
        })
      }
      // }
    }
  }
  // }
}
</script>
<style lang="less" scoped>
  @import "~styles/index.less";
  @import "~styles/variable.less";
.returnTop {
  position: fixed;
  width: 80/100rem;
  height: 80/100rem;
  background: rgba(0, 0, 0, 0.5);
  right: 16/100rem;
  bottom: 10%;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 200;
  .iconfont {
    color: #fff;
    font-size: 50/100rem;
  }
}
.myWallet{
  position: relative;
  top: 0;
  background: #fff;
  width: 100%;
  height:calc(100vh - 0.98rem);
}
/* */
.m_bg_top_t{
  position: relative;
  width: 750/100rem;
  height: 395/100rem;
  background:#fff;
  /* padding-top: 20/100rem; */
}
.m_bg_top_bg{
  position: absolute;
  width: 750/100rem;
  height: 327/100rem;
}
.m_bg_top_content{
  position: relative;
  top: 80/100rem;
  padding: 0 31/100rem;
  width: 625/100rem;
  height: 310/100rem;
  color: #a96c48;
}
.m_bg_top_content_bg{
  position: absolute;
  width: 687/100rem;
  height: 310/100rem;
}
.m_balance{
  position: relative;
  padding:10/100rem 20/100rem 0;
  display: flex;
  justify-content: space-between;
  height: 180/100rem;
  width: 100%;
}
.m_balance_l{
  width:50%;
}
.m_balance .m_balance_con{
  display: block;
  font-size: 0.3rem;
  padding-top: 40/100rem;
  padding-left: 15/100rem;
}
.m_balance .m_balance_count{
  display: block;
  font-size: 60/100rem;
  color: #fff;
  padding-top: 10/100rem;
  /* padding-top: 40/100rem; */
}
.m_balance_r{
  padding:30/100rem 10/100rem 0 0;
}
.m_balance_r .m_balance_r_t{
 font-size: 22/100rem;
 padding-left: 12/100rem;
}
.m_balance_r .m_balance_r_t span{

}
.m_balance_r .m_balance_r_t i{
  font-size: 24/100rem;
  display: inline-block;
  padding-left: 5/100rem;
}
.m_balance .m_cash{
  float: right;
  width: 132/100rem;
  font-size: 30/100rem;
  line-height: 50/100rem;
  background: #fff;
  text-align: center;
  border-radius: 10/100rem;
  margin-top: 20/100rem;
}
.m_all_money{
  position: relative;
  padding:0 31/100rem;
  clear: both;
  height: 70/100rem;
  line-height: 70/100rem;
  font-size: 26/100rem;
  width: 100%;
}
.m_all_money .m_y_money{
  float: left;
}
.m_all_money .m_n_money{
  float: right;
}

/*明细导航*/
.m_Detailed{
  background: #fff;
  position: relative;
  padding-top: 20/100rem;
}
.m_Detailed .detailed_nav {
  position: relative;
  height:89/100rem;
  line-height:89/100rem;
  width: 100%;
  /* clear: both; */
  display: flex;
  justify-content: space-around;
  border-bottom: 1/100rem solid #ececec;

}
.m_Detailed .detailed_nav div {
  color: #666;
  font-size: 30/100rem;
  text-align: center;
  height: 87/100rem;
  line-height: 87/100rem;
  /* float: left */
}
.m_Detailed .detailed_nav div:nth-child(1){
   padding: 0 40/100rem;
}
.m_Detailed .detailed_nav div:nth-child(2){
    padding: 0 40/100rem;
}
// .m_Detailed .detailed_nav view:nth-child(3){
//     float: right;
//     position: relative;
//     /* padding: 0 40/100rem;  */
// }
.m_Detailed .detailed_nav .text_active {
  border-bottom: 4/100rem solid #dfb68f;
  color: #dfb68f;
  padding: 0 .3rem;
